@use 'helpers' as *;

:root {
 --color-coriander: 187, 201, 159;
 --color-burnt-sienna: 230, 136, 7;
 --color-egg-sour: 255, 245, 218;
 --color-green-white: 241, 244, 236;
 --color-hampton: 226, 209, 166;
 --color-paarl: 170, 95, 45;
 --color-santa-fe: 188, 114, 74;
 --color-swamp-green: 163, 175, 132;
 --color-a: 214, 214, 214;
 --color-b: 187, 201, 159;

 --min-width: 400px;
}

a {
	color: unset;
	text-decoration: unset;
}

img {
 display: block;
 height: 100%;
 width: 100%;
}

em {
 font-style: italic;
}

.max-width {
 margin-inline: auto;
 max-width: var(--max-width);
}

.icon {
 display: inline-flex;
 vertical-align: middle;
}

.icon--bullet {
 margin-inline-end: 1ch;
}

.icon--pulse {
 animation: pulse 1s infinite alternate ease-in-out;
 background-color: red;
 border-radius: 9999px;
 color: white;
}

.icon > svg {
 height: 1em;
 width: 1em;
 stroke-width: 1.5;
}

.highlight {
 animation: skew-shift 2s infinite alternate ease-in-out;
 padding: 3px 6px;
}

svg-mask {
 --max-width: 1366px;

 display: flex;
 position: absolute;
 left: 0;
 top: 0;
 width: 100%;
}

svg-mask > svg {
 fill: rgb(var(--color-green-white));
 width: 100%;
 height: fluid(40px, 85px);
}

@keyframes pulse {
 from {
 box-shadow: 0 0 0 0.375em red;
 }
 to {
 box-shadow: 0 0 0 0.5em red;
 }
}

@keyframes skew-shift {
 from {
 background-color: rgba(var(--color-coriander), 33.33%);
 clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0% 100%);
 }
 to {
 background-color: rgba(var(--color-coriander), 66.66%);
 clip-path: polygon(4px 0%, 100% 0%, calc(100% - 4px) 100%, 0% 100%);
 }
}